.App .container {
    background: #fff;
    -webkit-box-shadow: 0 0 3px rgba(0,0,0,.1);
            box-shadow: 0 0 3px rgba(0,0,0,.1);
    width: 90%;
    height: 800px;
    margin: 100px auto;
    overflow: hidden;
    display: -ms-flexbox;
    display: flex;
}

.App .container .left {
    width: 25%;
    overflow: hidden;
    height: 800px;
}

.App .container .right {
    width: 70%;
    overflow: hidden;
    height: 800px;
}


