.Client {
    margin: 15px 50px;
    display: -ms-flexbox;
    display: flex;
}

.Client .avatar {
    width: 150px;
}

.Client .avatar img {
    width: 128px;
}

.Client .about {
    margin: 10px 0;
    color: #3f3f3f;
}

.Client .about div {
    margin: 0;
}

.Client .about div h3 {
    padding: 4px 0;
    color: #3f3f3f;
    border-top: solid 2px #fbfbfb;
    border-bottom: solid 2px #fbfbfb;
    font-size: 15px;
}

.Client .about div p span {
    color: #d8d8d8;
}

.Client .about div p {
    font-size: 13px;
}


