.Clients {
    height: 800px;
    border-right: solid 2px #fbfbfb;
    overflow-y: scroll;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
}

.Clients .client {
    margin: 0 10px 0 10px;
    width: 95%;
    border-bottom: solid 2px #fbfbfb;
    color: #3f3f3f;
    cursor: pointer;
}

.Clients .client:hover {
    background: #fdfdfd;
}

.Clients .activeClient {
    background: #f9f9f9;
}

.Clients .activeClient:hover {
    background: #f9f9f9;
}

.Clients .client .avatar {
    width: 20%;
    float: left;
    margin: 5px 10px 5px 5px;
}

.Clients .client .avatar img {
    max-width: 100%;
    border-radius: 50%;
}

.Clients .client .info {
    padding: 5px 0;
    color: #777;
    font-size: 15px;
}


